import { Value } from '@ali/4ever-cangjie';

export default function isSelectionInTaskList(value: Value) {
  const { selection, document } = value;
  const paragraph = document
    // @ts-ignore
    .getClosest(selection.getStart(document).key, (node) => node.type === 'paragraph');
  if (!paragraph || !paragraph.data) return false;
  const { list } = paragraph.data;
  if (!list) return false;
  const { isTaskList } = list;
  return isTaskList;
}
